import SubItem from "../../components/SubItem.vue"
import NewsItem from "../../components/NewsItem.vue"
export default {
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0,
			sublist: [],
			isShow: true,
			newslist: []
		};
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		closeAc() {
			this.isShow = false
		},
		toDetail(index) {
			console.log(index)
			uni.navigateTo({
				url: '../newsdetail/newsdetail?index=' + index,
				animationType: 'pop-in',
				animationDuration: 200
			})
		}
	},
	mounted() {
		uni.request({
				url: "http://rap2api.taobao.org/app/mock/234537/get/sub",
				success: (res) => {
					this.sublist = res.data.classify

				}
			}),
			uni.request({
				url: "http://rap2api.taobao.org/app/mock/234537/get/subnews",
				success: (res) => {
					let newsArr = res.data.subnews
					// console.log(newsArr);
					newsArr.forEach((item, i) => {
						if (item.pushtop) {
							newsArr.unshift(...newsArr.splice(i, 1))
						}
					})
					this.newslist = newsArr
					// console.log(newsArr)
				}
			})
	},
	components: {
		SubItem,
		NewsItem
	}
}
